import React, { useEffect, useRef } from "react";
import { Wrapper } from "@googlemaps/react-wrapper";
import styled from "styled-components";

const GoogleMapsContainer = styled.div`
  height: 100vh;
  width: 100vw;
`;

export const App = ({ devices = [] }) => {
  return (
    <Wrapper apiKey={"AIzaSyABULryR5SmKNEe5yeAV9fWLtTjwr2hVNU"}>
      <GoogleMaps>
        {devices.map(({ serial, latitude, longitude }) => (
          <Marker
            key={`${serial}-${longitude}`}
            position={{ lat: latitude, lng: longitude }}
          />
        ))}
      </GoogleMaps>
    </Wrapper>
  );
};

function GoogleMaps({ children }) {
  const ref = useRef(null);

  const [map, setMap] = React.useState();
  const [kmlLayer, setKmlLayer] = React.useState();

  useEffect(() => {
    if (ref.current && !map) {
      setMap(
        new window.google.maps.Map(ref.current, {
          center: { lat: 30.796482861007384, lng: -8.879377841949463 },
          zoom: 8,
          mapTypeId: "hybrid",
          disableDefaultUI: true,
        })
      );
    }
  }, [ref, map]);

  useEffect(() => {
    if (map && !kmlLayer) {
      console.log("new kml");
      const ctaLayer = new window.google.maps.KmlLayer({
        url: "http://localhost:3000/R%C3%A9seau%20ABHSM%20ALL.kml",
        map: map,
      });
      setKmlLayer(ctaLayer);
    }
  }, [ref, map, kmlLayer]);

  return (
    <>
      <GoogleMapsContainer ref={ref} />
      {map &&
        React.Children.map(children, (child) => {
          if (React.isValidElement(child)) {
            return React.cloneElement(child, { map });
          }
        })}
    </>
  );
}

const Marker = ({ color, ...options }) => {
  const [marker, setMarker] = React.useState();

  React.useEffect(() => {
    if (!marker) {
      setMarker(new window.google.maps.Marker());
    }

    // remove marker from map on unmount
    return () => {
      if (marker) {
        marker.setMap(null);
      }
    };
  }, [marker]);
  React.useEffect(() => {
    if (marker) {
      const iconUrl = `http://maps.google.com/mapfiles/ms/icons/${color}-dot.png`;
      marker.setOptions({
        ...options,
        icon: { url: iconUrl, scaledSize: { width: 50, height: 50 } },
      });
    }
  }, [marker, options, color]);
  return null;
};
